<template>
  <div class="container">
    <HeadTop :title="title" :isShowTop="false" :isSubHeader="true"></HeadTop>
    <div id="webview">
      <iframe :src="_route.query.href" scrolling="auto" frameborder="0" id="iframe"></iframe>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useRouter } from 'vue-router'
export default {
  components: {
    HeadTop
  },
  setup () {
    const { t } = useI18n()
    const router = useRouter()
    const data = reactive({
      $t: t,
      loading: false,
      title: '',
      _route: router.currentRoute['_value']
    })
    onMounted(() => {
      var _iframe = document.getElementById('iframe').contentWindow;
      console.log(_iframe)
      _iframe.onload = () => {
        data.title = _iframe.document.title || 'Swap'
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
#webview {
  height: calc(100vh - 44px);
  iframe {
    width: 100%;
    height: 100%;
  }
}
</style>
